<template>
  <div id="transmit">
    <common-header :title="'送给好友'"></common-header>
    <div class="transmit__header">
      <Gift />
    </div>
    <div class="transmit__input">
      <Cell :title="'赠送数量'" :type="'count'" :num="value" :max="5" @change="valueChange"/>
      <Cell :title="'选择好友'" @click="select"/>
      <div class="input__textrea">
        <div class="textrea__title">礼物祝福</div>
        <MyTextArea @change="getValue" :maxlength="100" :placeholder="'给朋友送个祝福吧！'"/>
      </div>
    </div>
    <UploadImg />

    <div class="submit__btn" :class="{'active': true}">送给好友</div>
  </div>
</template>

<script>
  import Gift from '@/components/Goods/Goods';
  import Cell from '@/components/Cell/Cell';
  import MyTextArea from '@/components/TextArea/TextArea';
  import UploadImg from '@/components/UploadImg/UploadImg';

  export default {
    data() {
      return {
        value: 3, //商品数量
      }
    },
    methods: {
      valueChange(val) {
        this.value += val;
      },

      //获取文本内容
      getValue(val) {
        console.log(val);
      },

      select() {
        console.log('选择好友')
      }
    },
    components: {
      Gift,
      Cell,
      UploadImg,
      MyTextArea
    }
  }
</script>

<style lang="scss">
  #transmit {
    .transmit__header {
      border-bottom: 1px solid #eee;
    }

    .input__textrea {
      padding-top: 0.3rem;
      border-top: 1px solid #eee;

      .textrea__title {
        padding-left: 0.4rem;
        font-size: 0.427rem;
        color: #333;
      }
    }
  }
</style>




