<template>
  <div class="refund-text" id="textArea">
    <textarea name="refund" :placeholder="placeholder" v-model="value" :maxlength="maxlength" @input="getTextarea" ref="textArea"></textarea>
    <span v-if="maxlength">{{value.length}}/{{maxlength}}</span>
  </div>
</template>

<script>
  export default {
    props: ['maxlength', 'placeholder'],

    data() {
      return {
        value:'',
      }
    },

    methods: {
      getTextarea(event) {
        this.$emit('change', this.value);
      }
    }
  }
</script>


<style lang="scss">
  #textArea {
    position: relative;
    padding:0.27rem 0.4rem;
    textarea{
      background: #fff;
      border: 0;
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      resize: none;
      font-size: 0.37rem;
      color: #333;
      line-height: 0.53rem;
      height: 2.08rem;
    }

    >span{
      position: absolute;
      right: 0.4rem;
      bottom: 0rem;
      font-size: 0.35rem;
      color: #999;
    }
  }
</style>



